<template>
  <div class="block">
    <svg v-if="this.$vssWidth > 1170" viewBox="0 0 1170 248" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path
          d="M0 4C0 1.79086 1.74928 8.7021e-07 3.95842 1.86623e-06C19.5842 8.91136e-06 77.4676 3.19146e-05 105 0C137 -3.70932e-05 137 24 137 24C137 24 137 0 169 0C199.85 0 1101.54 0 1166.33 0C1168.54 0 1170 1.79086 1170 4V244C1170 246.209 1168.21 248 1166 248H4C1.79086 248 0 246.209 0 244V4Z"
          fill="white"/>
    </svg>
    <svg v-if="this.$vssWidth <= 1170" viewBox="0 0 272 300" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path
          d="M0 4C0 1.79086 1.74966 0 3.9588 0C19.456 0 76.4993 0 104 0C136 2.87703e-10 136 24 136 24C136 24 136 0 168 0C195.501 0 252.544 0 268.041 0C270.25 0 272 1.79086 272 4V296C272 298.209 270.209 300 268 300H4C1.79086 300 0 298.209 0 296V4Z"
          fill="white"/>
    </svg>

    <img src="/static/img/home/1/1.png" alt="" class="leaf">
    <div class="flex-between align-center">
      <div class="title">Работаем только <br>с профессиональной <br>косметикой</div>
      <div v-if="this.$vssWidth > 1170 && !loading" class="flex-start align-center">
        <div class="item" v-for="brand in items" :key="brand.id">
          <img :src="getSrc(brand.thumb)" :alt="brand.name">
        </div>
      </div>
      <VueSlickCarousel
          class="block1-slider"
          v-if="this.$vssWidth <= 1170 && !loading"
          :arrows="false"
          :dots="false"
          :draggable="true"
          :swipe="true"
          :variableWidth="true"
      >
        <div class="item" v-for="brand in items" :key="brand.id">
          <img :src="getSrc(brand.thumb)" :alt="brand.name">
        </div>
      </VueSlickCarousel>
      <Loader class="loader" v-if="loading"></Loader>
    </div>
  </div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import Loader from "@/components/Loader";
import api from "@/api";
import {createImageUrl} from "@/utils";

export default {
  name: "Brands",
  components: {VueSlickCarousel, Loader},
  props: ['category'],
  data: () => ({
    items: [],
    loading: false
  }),
  methods: {
    async getItems() {
      this.loading = true
      this.items = await api.getBrands(this.category)
      this.loading = false
    },
    getSrc(src) {
      return createImageUrl(src)
    }
  },
  created() {
    this.getItems()
  }
}
</script>

<style scoped>
.loader {
  flex: 1 1 0;
}
</style>